.modal__container {
  max-width: 430px;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  color: #000;
  position: relative;
}

.modal__container:hover {
  cursor: unset;
}

@media screen and (max-width: 425px) {
  .modal__container {
    max-width: 240px;
  }
}
